import HomeIcon from '@material-ui/icons/Home'
import CameraIcon from '@material-ui/icons/Camera'
import SettingsIcon from '@material-ui/icons/Settings'
import {
	lazy
} from 'react'
const Home = lazy(() =>
	import ('../pages/home'))
const Share = lazy(() =>
	import ('../pages/share'))
const Config = lazy(() =>
	import ('../pages/config'))


export const routes = [{
	path: '/zone',
	name: 'home',
	component: Home,
	exact: true,
	title: '首页',
	icon: HomeIcon
}, {
	path: '/zone/share',
	name: 'share',
	component: Share,
	exact: true,
	title: '朋友圈',
	icon: CameraIcon
}, {
	path: '/zone/config',
	name: 'config',
	component: Config,
	exact: true,
	title: '配置页',
	icon: SettingsIcon
}]